import React, {FC, useState} from 'react'
import {Box, Button, FormControlLabel, Radio, RadioGroup, Typography} from "@mui/material";
import {useDispatch, useSelector} from "react-redux";
import {AppDispatch} from "../../../store/store";
import {
    decrementStep,
    incrementStep,
    setCallData,
    setPassData
} from "../../../store/slices/pick-up-slice";
import {PickUpSelector} from "../../../store/selectors/selectors";
import CustomAlert from "../../../components/alert";

const Call: FC = () => {
    const {call} = useSelector(PickUpSelector)
    const dispatch = useDispatch<AppDispatch>()

    const [open, setOpen] = useState(false)

    const handleBack = () => {
        dispatch(decrementStep())
    }

    const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        const data = new FormData(event.currentTarget);
        const call = data.get('call') as string

        if (call) {
            dispatch(setCallData({call}))
        } else {
            setOpen(true)
        }
    }

    return (
        <>
            <CustomAlert open={open} setOpen={setOpen}/>
            <Typography variant="h6" gutterBottom>
                   :
            </Typography>
            <Box component="form" onSubmit={handleSubmit}>
                <RadioGroup
                    aria-labelledby="pick-up-call"
                    name="radio-buttons-group"
                    defaultValue={call}
                >
                    <FormControlLabel name="call" value="15 " control={<Radio/>} label="  15 "/>
                    <FormControlLabel name="call" value="30 " control={<Radio/>} label="  30 "/>
                    <FormControlLabel name="call" value="1 " control={<Radio/>} label="  "/>
                    <FormControlLabel name="call" value="3 " control={<Radio/>} label="  3 "/>
                    <FormControlLabel name="call" value=" " control={<Radio/>} label="  "/>
                </RadioGroup>
                <Box sx={{display: 'flex', justifyContent: 'space-between', mt: 3}}>
                    <Button onClick={handleBack}>
                        
                    </Button>
                    <Button variant="contained" type="submit">
                        
                    </Button>
                </Box>
            </Box>
        </>
    )
}

export default Call
